
import {
    IonItem,
    IonImg,
    IonThumbnail,
    IonLabel,
    IonIcon,
    IonRadio
} from '@ionic/vue';
import { useRouter } from "vue-router";
import { lockClosedOutline, readerOutline, trashOutline } from 'ionicons/icons';
import { computed, defineComponent } from 'vue';

export default defineComponent({
    props: [
        "collections",
        "isSelectionMode"
    ],
    components: {
        IonItem,
        IonImg,
        IonThumbnail,
        IonLabel,
        IonIcon,
        IonRadio
    },
    setup(props) {
        const isInSelectionMode = computed(() => props.isSelectionMode);
        const thumbnailPlaceholder = computed (() => require('../../assets/placeholder_square_small.png'))
        const router = useRouter();
        const goToCollectionPage = (collection: any) => {
            router.push({
                name: 'collection',
                params: {
                    id: collection.id
                },
            });
        }
        return {
            thumbnailPlaceholder,
            lockClosedOutline,
            readerOutline,
            trashOutline,
            goToCollectionPage,
            isInSelectionMode
        }
    },
})
