
import {
    useTainacanStore
} from '../store/storeTainacan';
import { ref } from 'vue';

import CollectionsList from '@/components/lists/CollectionsList.vue';
import ItemsList from '@/components/lists/ItemsList.vue';
import BaseLayout from '@/components/base/BaseLayout.vue';
import { arrowForwardOutline } from "ionicons/icons";
import {
    IonIcon,
    IonButton,
    IonLoading,
    IonList,
    IonListHeader,
    IonRefresher,
    IonRefresherContent
} from '@ionic/vue';

export default {
    components: {
        IonIcon,
        CollectionsList,
        ItemsList,
        BaseLayout,
        IonList,
        IonListHeader,
        IonButton,
        IonLoading,
        IonRefresher,
        IonRefresherContent
    },
    setup() {
        const isLoading = ref(false);
        const setIsLoading = (state: boolean) => isLoading.value = state;
        const loadCollectionsAndItems = async () => {
            await tainacanStore.fetchHomeCollections();
            await tainacanStore.fetchHomeItems();
        }
        const doRefresh = async (event: any) => {
            await loadCollectionsAndItems();
            if (event && event.target)
                event.target.complete();
        }

        let tainacanStore = useTainacanStore();

        return {
            arrowForwardOutline,
            tainacanStore,
            isLoading,
            setIsLoading,
            loadCollectionsAndItems,
            doRefresh
        }
    },
    async created() {
        this.setIsLoading(true);
        await this.loadCollectionsAndItems();
        this.setIsLoading(false);
    }
}

