
import { logOutOutline } from "ionicons/icons";
import { 
    useWpStore
} from '../../store/storeWp';

import {
    IonPage,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonContent,
    IonIcon,
    IonBackButton,
    IonButton,
    IonButtons,
    IonImg,
    IonToast
} from '@ionic/vue';
import { computed } from 'vue';
import { useTainacanStore } from '@/store/storeTainacan';

export default {
    props: ['pageTitle', 'pageDefaultBackLink'],
    components: {
        IonPage,
        IonHeader,
        IonToolbar,
        IonTitle,
        IonContent,
        IonIcon,
        IonButton,
        IonBackButton,
        IonButtons,
        IonImg,
        IonToast
    },
    setup(){
        const tainacanLogo = computed (() => require('../../assets/logo.png'))
        let wpStore = useWpStore();
        let tainacanStore = useTainacanStore();

        return {
            tainacanLogo,
            wpStore,
            logOutOutline,
            tainacanStore
        }  
    },
    methods: {
        async logOff(){
            await this.wpStore.logoff();
            this.$router.go();
        }
    }
}
